import { Token } from '@cetus/types'
import { useEffect, useState } from 'react'
import { isHoneyPot } from 'suihoneypot'

/**
 * token È£éÈô©ÊèêÁ§∫Ê£ÄÊü•
 * @returns
 */
export default function useCheckTokenScamsAlert(coinInfo?: Token) {
  const [isScamsCoin, setIsScamsCoin] = useState<boolean | undefined>()

  const checkIsScamsCoin = async (coinInfo: Token) => {
    const is_verified = coinInfo.is_verified === undefined ? coinInfo.is_trusted : coinInfo.is_verified
    if (is_verified) {
      return false
    }

    try {
      const isScamsCoin = await isHoneyPot(coinInfo.coin_type)

      console.log('üöÄ ~ file: useCheckTokenScamsAlert.ts:24 ~ checkIsScamsCoin ~ isScamsCoin:', isScamsCoin)

      return isScamsCoin
    } catch (error) {
      console.log('üöÄ ~ file: useCheckTokenScamsAlert.ts:19 ~ checkTokenScams ~ error:', error)
    }

    return false
  }

  useEffect(() => {
    setIsScamsCoin(undefined)
    if (coinInfo) {
      checkIsScamsCoin(coinInfo).then(res => {
        setIsScamsCoin(res)
      })
    }
  }, [coinInfo?.coin_type])

  return {
    isScamsCoin
  }
}
